'use client'
import { Archivo_Black } from 'next/font/google'
import React, { useState } from 'react'
import LogotypeBanner from 'src/components/logotypeBanner.component'
import { cn } from 'src/lib/utils'

const font = Archivo_Black({
	variable: '--font-sans',
	weight: '400',
})

type indeedStatus = 'constructive' | 'cautionary' | 'destructive'
const logotypeName = 'Alterworld'
const logInfoPrompt = '[alw:info] '
const commandlineEntries: {
	isPrompt?: boolean
	status?: indeedStatus
	log?: string
}[] = [
	{ log: `alternate --commit $this` },
	{ status: 'cautionary', log: `make pattern --destructive` },
	{ log: `sv & > .backup` },
	{ isPrompt: true, log: `$ > ` },
]

const terminalShell = (
	<ul className="max-w-96 marker:text-constructive list-disc list-inside bg-card/50 backdrop-blur-sm rounded-b-md w-full mt-0.5 px-2 divide-y-2">
		{commandlineEntries.map((commantlineEntry, index) => (
			<li key={index} className={`marker:text-${commantlineEntry.isPrompt ? 'accent' : commantlineEntry.status}`}>
				{!commantlineEntry.isPrompt && logInfoPrompt}
				{commantlineEntry.log}
				{commantlineEntry.isPrompt && <span className="bg-destructive">_</span>}
			</li>
		))}
	</ul>
)

const HomePage: React.FC<Readonly<{}>> = () => {
	const mouseMoveHandler = event => {
		setMousePosition({
			x: event.clientX,
			y: event.clientY,
		})
	}
	const [mousePosition, setMousePosition] = useState<{ x: number; y: number }>({
		x: 0,
		y: 0,
	})

	return (
		<>
			<section className="perspective-1000 overflow-hidden" onMouseMove={mouseMoveHandler}>
				<div className="h-screen transform-style-3d">
					<div
						className={cn(`absolute -inset-20 bg-cover bg-center bg-city-wallpaper`)}
						style={{
							transform: `rotateX(${(mousePosition.y - window.innerWidth / 2) * -0.005}deg) rotateY(${(mousePosition.x - window.innerHeight / 2) * -0.01}deg) translateZ(${0}rem)`,
						}}
					></div>
					<div
						className="absolute inset-0 bg-cover bg-center rotate-y-2 flex items-center justify-center"
						style={{
							transform: `rotateX(${(mousePosition.y - window.innerWidth / 2) * -0.005}deg) rotateY(${(mousePosition.x - window.innerHeight / 2) * -0.01}deg) translateZ(${10}rem)`,
						}}
					>
						<LogotypeBanner />
					</div>
					<div
						className="absolute inset-0 mt-40 bg-cover bg-center rotate-y-2 flex items-center justify-center gap-4"
						style={{
							transform: `rotateX(${(mousePosition.y - window.innerWidth / 2) * -0.005}deg) rotateY(${(mousePosition.x - window.innerHeight / 2) * -0.01}deg) translateZ(${5}rem)`,
						}}
					>
						workspace
						{terminalShell}
					</div>
					<div
						className="absolute inset-0 mt-40 bg-cover bg-center rotate-y-2 flex items-center justify-center gap-4"
						style={{
							transform: `rotateX(${(mousePosition.y - window.innerWidth / 2) * -0.005}deg) rotateY(${(mousePosition.x - window.innerHeight / 2) * -0.01}deg) translateZ(${5}rem)`,
						}}
					>
						workspace
					</div>
				</div>
			</section>
		</>
	)
}

export default HomePage
