'use client'

import React, { useEffect, useState } from 'react'
import Terminal, { ColorMode, TerminalOutput } from 'react-terminal-ui'
import { TypeAnimation } from 'react-type-animation'
import { cn } from 'src/lib/utils'

const TerminalCardLogger: React.FC<Readonly<{}>> = () => {
	const [terminalLineData, setTerminalLineData] = useState<React.ReactNode[]>([])
	const logMessages = [
		'Server started on port 3000',
		'Database connected successfully',
		'GET\trequest received at /api/users',
		'POST\trequest received at /api/users',
		'User authenticated successfully',
		'Error\t\tUnable to connect to database',
		'Warning\ttHigh memory usage detected',
		'Info\tServer uptime is 24 hours',
	]

	const addLog = () => {
		const randomColor = `#${Math.floor(Math.random() * 16777215).toString(16)}`
		const date = new Date()
		const formattedDate = `${date.getDate()}:${date.getMonth() + 1}:${date.getFullYear()}`
		const message = `${formattedDate}\t${logMessages[Math.floor(Math.random() * logMessages.length)]}`

		const lineData = (
			<TerminalOutput
				children={<TypeAnimation cursor={false} sequence={[message, 1000]} style={{ color: randomColor }} />}
			/>
		)

		setTerminalLineData(prevState => [...prevState, lineData])
	}

	const terminalInputHandler = (terminalInput: string) => {
		setTerminalLineData(prevState => [...prevState, <TerminalOutput>{terminalInput}</TerminalOutput>])
	}

	useEffect(() => {
		const logInterval = setInterval(addLog, 2000)
		return () => clearInterval(logInterval)
	}, [])

	return (
		<div className={cn(`max-w-[720px] w-full`)}>
			<Terminal
				height="200px"
				colorMode={ColorMode.Dark}
				onInput={terminalInputHandler}
				children={terminalLineData}
			/>
		</div>
	)
}

export default TerminalCardLogger
